<template>
	<ul class="data-list">
		<van-loading v-if="loading" type="spinner" size="24" />
		<template v-else>
			<div v-if="nodata" class="no-data"></div>
			<template v-else>
				<li v-for="(item, i) in data" :key="i" @click="onClick(item)">
					<div class="item-img">
						<van-image :src="item.icon">
							<template v-slot:loading>
								<van-loading type="spinner" size="20" />
							</template>
						</van-image>
					</div>
					<div class="item-content">
						<div class="title">{{item.examinationName}}</div>
						<div class="more">
							<span>课程：{{item.cultivateType}}</span>
						</div>
						<div class="more">
							<span>考试时限：{{item.examinationTimeLimit+'分钟'}}</span>
							<span>及格分：{{item.passingScoreLine}}</span>
						</div>
						<!--timingMode 0:考试时间开始计时  1:打开试卷时间计时-->
						<div class="time">
							考试开始时间：
							<template v-if="!item.timingMode">{{formatDate(item.examinationTime)}}</template>
							<template v-else>打开考卷开始计时</template>
						</div>
						<div class="time" v-if="item.makeupTime">补考最后期限：{{formatDate(item.makeupTime)}}</div>
					</div>
					<van-icon name="arrow" />
				</li>
			</template>
		</template>
	</ul>
</template>

<script>
	import { mapState } from "vuex"
	const icon = require('../../assets/images/test.png')
	export default {
		name: 'examlist',
		data() {
			return {
				loading: true,
				nodata: false,
				data: []
			}
		},
		computed: {
			...mapState(['info'])
		},
		mounted() {
			this.getData()
		},
		methods: {
			formatDate(date) {
				date = this.$Utils.toDate(date)
				return this.$Utils.dateFtt('yyyy-MM-dd hh:mm:ss', date)
			},
			// 培训ID获取评估
			async getData() {
				let params = {
					studentId: this.info.id,
					cultivateId: this.$route.query.id,
					type: 0
				}
				let res = await this.$Request.get('/examination/getExaminationByWeChat.do', params)
				this.loading = false
				if(res && res.code === 0) {
					let data = res.list || []
					data.forEach(item => { item.icon = icon })
					this.data = data
					this.nodata = !this.data.length
				} else {
					this.nodata = true
				}
			},
			onClick(item) {
				let time = new Date(item.examinationTime)
				let end = new Date(item.endTime)
				let route = {
					path: '/exam',
					query: {
						id: this.$route.query.id,
						examid: item.id
					}
				}
				this.$router.push(route)
			}
		}
	}
</script>

<style>

</style>


